<script>
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../../utils/Util'

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Tanda Tangan Sertifikat',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Tanda Tangan Sertifikat'+ ' ' +this.$route.params.action,
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			aksiEdit: false,
      		aksiSave: false,
			alert:false,
			selectedFilebg: "",
			selectedFilettd: "",
		}
	},
	mounted() {
		console.log("alamat", Utils.Host())
		this.Getdata()
		if (this.$route.params.action === 'tambah') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'edit') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'detail') {
		this.aksiEdit = false
		this.aksiSave = false
    }
  },
//   computed: {
//   },
  methods: {
    async Getdata() {
      try {
        const action = this.$route.params.action
        if (action === 'detail' || action === 'edit') {
          let response = await axios.get(this.api+"/sertifikatref/"+this.$route.params.id, Utils.authHeader());
          this.data = response.data.Data[0];
        }
      } catch (err) {
          console.error(err);
      }
    },
    async openEdit(id) {
        document.location = '/tandatangansertifikataction/edit/' + id
        // location.reload();
    },
	async onUploadFilebg() {
		let rawImg;
		const file = document.querySelector('input[name=bg]').files[0];
		const reader = new FileReader();

		reader.onloadend = async () => {
		rawImg = reader.result;
			//
			const target = { f_background: rawImg }
			
			var res = null;
			res = await axios.post(
				this.api + "/sertifikatrefbg/"+this.$route.params.id,
				target,
				Utils.authHeader()
			);
			if (res.status === 200) {
				this.alert = true
				this.message = res.data.Error_desc
				location.reload();
			} else {
				this.alert = true
				this.message = res.data.Error_desc
			}
			//
		}
		reader.readAsDataURL(file);	

    },
	async onUploadFiletd() {
		let rawImg;
		const file = document.querySelector('input[name=td]').files[0];
		const reader = new FileReader();
		reader.onloadend = async () => {
			rawImg = reader.result;
			//
			const target = { f_tandatangan: rawImg }
			
			var res = null;
			res = await axios.post(
				this.api + "/sertifikatreftd/"+this.$route.params.id,
				target,
				Utils.authHeader()
			);
			if (res.status === 200) {
				this.alert = true
				this.message = res.data.Error_desc
				location.reload();
			} else {
				this.alert = true
				this.message = res.data.Error_desc
			}
			//
		}
		reader.readAsDataURL(file);	

    },
    validation() {
      if (!this.data.f_keterangan) {
          return { code: 500, message: 'Keterangan Data ini harus diisi' }
      } else if (!this.data.f_jenis) {
          return { code: 500, message: 'Jenis Data ini harus diisi' }
      } else {
          return { code: 200, message: '' }
      }
    },
    async onSave() {
        const validation = this.validation()
        if (validation.code === 200) {
        var res = null
        const action = this.$route.params.action;
		const target = { f_keterangan: this.data.f_keterangan, f_jenis: this.data.f_jenis, f_pejabat: this.data.f_pejabat, f_tahun: this.data.f_tahun, f_nama: this.data.f_nama, f_kota: this.data.f_kota, f_color: this.data.f_color  };
        if (action === 'tambah') {
            res = await axios.post(this.api+"/sertifikatref", target, Utils.authHeader());
        } else {
            res = await axios.put(this.api+"/sertifikatref/"+ this.$route.params.id, target, Utils.authHeader());
        }
        if (res.data.Error_code === 0) {
			this.alert = true
			this.message = res.data.Error_desc
            document.location = '/tandatangansertifikataction/detail/' + res.data.Data[0].f_sertifikat_id;
            this.Getdata();
            this.aksiEdit = !this.aksiEdit
            this.aksiSave = false
        }  else {
			this.alert = true
			this.message = res.data.Error_desc
        }
        } else {
			this.alert = true
			this.message = validation.message
        }
    },
  },
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									
									<b-alert
											v-model="alert"
											variant="success"
											dismissible
											>{{ message }}</b-alert
										>

									<br>&nbsp;&nbsp;
									<br>
									<div class="table-responsive">

										<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Jenis"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_jenis"
										type="text"
										required
										placeholder="Jenis"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tahun"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_tahun"
										type="text"
										required
										placeholder="Tahun"
									></b-form-input>
									</b-form-group>
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Keterangan"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_keterangan"
										type="text"
										required
										placeholder="Keterangan"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Pejabat"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_pejabat"
										type="text"
										required
										placeholder="Pejabat"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nama"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_nama"
										type="text"
										required
										placeholder="Nama"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Kota"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_kota"
										type="text"
										required
										placeholder="Kota"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Color"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_color"
										type="text"
										required
										placeholder="Color"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Background"
										label-for="input-horizontal"
									>
									<input type="file" name="bg">
									<button
									@click="onUploadFilebg"
									label="Simpan"
									>Upload</button>
									<img
									:src="data.f_background"
									width="100%"
								/>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tanda Tangan"
										label-for="input-horizontal"
									>
									<input type="file" name="td">
									<button
									@click="onUploadFiletd"
									label="Simpan"
									>Upload</button>
									<img
									:src="data.f_tandatangan"
									width="100%"
								/>
									</b-form-group>


									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label-for="input-horizontal"
									>
									<b-button variant="success" @click="onSave(data.f_sertifikat_id)" v-if="aksiEdit">Simpan Data</b-button>
									&nbsp;
									<b-button variant="danger" @click="openEdit(data.f_sertifikat_id)" v-if="!aksiEdit">Edit Data</b-button>
									</b-form-group>									

									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
